<template>
<div>
<h2 style="text-align: center;"></h2>
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="lonfenner-product-ruleForm" size="small" >
  <el-form-item label="" prop="filePath">
    <el-input v-model="ruleForm.filePath"></el-input>
  </el-form-item>
  <el-form-item label="" prop="property">
    <el-input v-model="ruleForm.property"></el-input>
  </el-form-item>
  <el-form-item label="" prop="attachProperty">
    <el-input v-model="ruleForm.attachProperty"></el-input>
  </el-form-item>
  <el-form-item label="" prop="attachType">
    <el-radio-group v-model="ruleForm.attachType">
      <el-radio :label="0"></el-radio>
      <br>
      <el-radio :label="1" style="margin-top: 8px"></el-radio>
      <br>
      <el-radio :label="2" style="margin-top: 8px"></el-radio>
      <br>
      <el-radio :label="3" style="margin-top: 8px"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="" prop="type">
    <el-radio-group v-model="ruleForm.type">
      <el-radio :label="0"></el-radio>
      <el-radio :label="1">txt</el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="txt" prop="productNameFile" v-if="ruleForm.type==1">
    <el-input v-model="ruleForm.productNameFile"></el-input>
  </el-form-item>
  <el-form-item label="" prop="priceType">
    <el-radio-group v-model="ruleForm.priceType">
      <el-radio :label="0">,(</el-radio>
      <el-radio :label="1">,</el-radio>
      <el-radio :label="2"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="" prop="customDefs">
    <div>
      <el-checkbox v-model="ruleForm.customDefCheck"></el-checkbox>
      <el-input v-model="ruleForm.customDefs.code" style="width: 140px;margin-left: 20px;"></el-input>
      <el-input v-model="ruleForm.customDefs.name" style="width: 140px"></el-input>
      <el-input v-model="ruleForm.customDefs.valueOptions" style="width: 286px"></el-input>
    </div>
  </el-form-item>
  <el-form-item label="Authorization" prop="cookie">
    <el-input type="textarea" rows="12" v-model="ruleForm.cookie"></el-input>
  </el-form-item>
  <el-form-item class="content_button">
    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    <el-button  @click="resetForm('ruleForm')"></el-button>
  </el-form-item>
  <el-dialog
  title=""
  :visible.sync="dialogVisible"
  :close-on-click-modal="false"
  :close-on-press-escape="false"
  width="40%" center>
  <p v-if="running" style="color:#409EFF;text-align:center"></p>
  <p v-if="!running" style="color:#67C23A;text-align:center"></p>
  <p style="color:#909399;text-align:center"></p>
  <div v-for="(item,index) in uploadName" :key="1000+index">
    <span>{{item}}</span>
  </div>
  <p style="color:#F56C6C;text-align:center"></p>
  <div v-for="(item,index) in failName" :key="2000+index">
    <span>{{item}}</span>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button size="mini" type="primary" @click="dialogVisible = false"></el-button>
  </span>
</el-dialog>
</el-form>
</div>
</template>
<script>
import { Message } from 'element-ui'
import { productSend, productResult } from '@/api'
export default {
  data () {
    return {
      dialogVisible: false,
      runningStatusTimer: '',
      running: true,
      failName: [],
      uploadName: [],
      ruleForm: {
        type: 1,
        priceType: 2,
        attachType: '',
        filePath: '',
        property: '',
        attachProperty: '',
        productNameFile: '',
        cookie: '',
        customDefCheck: false,
        customDefs: {
          code: 'Size',
          name: '',
          valueOptions: 'M,L'
        }
      },
      rules: {
        filePath: [
          { required: true, message: '', trigger: 'blur' }
        ],
        property: [
          { required: true, message: '', trigger: 'blur' }
        ],
        attachProperty: [
          { required: true, message: '', trigger: 'blur' }
        ],
        attachType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        cookie: [
          { required: true, message: 'Authorization', trigger: 'blur' }
        ]
      }
    }
  },
  // 
  beforeDestroy () {
    if (this.runningStatusTimer) {
      clearInterval(this.runningStatusTimer)
    }
  },
  methods: {
    submitForm (formName) {
      if (this.ruleForm.type === 1 && !this.ruleForm.productNameFile) {
        Message({
          message: 'txttxt',
          type: 'error',
          duration: 3 * 1000,
          showClose: true
        })
        return
      }
      if (this.ruleForm.customDefCheck === true && !(this.ruleForm.customDefs.code && this.ruleForm.customDefs.name && this.ruleForm.customDefs.valueOptions)) {
        Message({
          message: '',
          type: 'error',
          duration: 3 * 1000,
          showClose: true
        })
        return
      }
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.ruleForm.customDefCheck !== true && (this.ruleForm.customDefs.code || this.ruleForm.customDefs.name || this.ruleForm.customDefs.valueOptions)) {
            this.$confirm('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.sendProduct()
            }).catch(() => {
            })
          } else {
            this.sendProduct()
          }
        } else {
          return false
        }
      })
    },
    async sendProduct () {
      const res = await productSend(this.ruleForm)
      if (res.status) {
        this.dialogVisible = true
        this.running = true
        this.failName = []
        this.uploadName = []
        this.runningStatusTimer = setInterval(() => {
          this.getProcessRunningStatus()
        }, 5000)
      }
    },
    async getProcessRunningStatus () {
      const res = await productResult(this.ruleForm)
      if (res.result) {
        this.running = res.result.running
        this.failName = res.result.failName
        this.uploadName = res.result.uploadName
        if (!res.result.running) {
          clearInterval(this.runningStatusTimer)
        }
      }
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
<style lang="scss">
.lonfenner-product-ruleForm{
  width: 800px;
  margin: 0 auto;
  .content_button{
    margin-left: 260px;
  }
  .content_radio{
    margin: 8px;
    float: left;
  }
}
</style>



