let baseURL
// 
if (process.env.NODE_ENV === 'development') { // 
  baseURL = '/api' // 
} else { // 
  if (process.env.type === 'test') { // 
    baseURL = 'http://localhost:8080'
  } else { // 
    baseURL = 'http://localhost:8080'
  }
}

export { baseURL }



